const EST_FRAC_KEPT = 1/6;

function getDefault(obj, key, defaultValue) {
  if (key in obj) {
    return obj[key];
  } else {
    return defaultValue;
  }
}

function setDefault(obj, key, defaultValue) {
  if (!(key in obj)) {
    obj[key] = defaultValue;
  }
}

function setDefaultThenAdd(obj, key, defaultValue, summand) {
  setDefault(obj, key, defaultValue);
  obj[key] += summand;
}

