class RulesModal extends React.Component {
  constructor(props) {
    super(props);
    this.setShowRulesFalse = this.setShowRulesFalse.bind(this);
  }
  render() {
    return (
      <div className={"modal " + (this.props.showRules ? "show" : "hide")}>
        <div className="main-column">
          <div className="modal-header">
            <div className="modal-close-button" />
            <div className="modal-title">How to Play</div>
            <button className="modal-close-button" value="close-rules" onClick={this.setShowRulesFalse}>&times;</button>
          </div>
          <div className="modal-content">
            <p><strong>Absurdliar</strong> is a game for people who like <a href="https://www.nytimes.com/games/wordle/index.html" target="_blank" rel="noreferrer">Wordle</a> (by Josh Wardle) or the game show Lingo, but want a harder challenge.
            Your goal is to guess the secret word. After each guess, the computer will give you a clue by lighting up letters. For example,</p>
            <div className="gameboard-explainer">
              <GameboardRow wordInfo={{guessedWord: "crane", pattern: [MISS, HIT, CLOSE, MISS, MISS].join('')}} lettersSatisfied={ALL_SATISFIED} />
            </div>
            {/* <p>this clue tells you that "R" is in the secret word in the second position, that "A" is somewhere in the secret word (but not the third position), and "C", "N", and "E" are not in the secret word at all.</p> */}
            <p>- <strong>R</strong> is in the secret word in the second position.<br />
            - <strong>A</strong> is in the secret word somewhere.<br />
            - <strong>C</strong>, <strong>N</strong>, and <strong>E</strong> are not in the secret word.</p>
            <p><strong>But there's a catch.</strong> Actually, two catches:</p>
            <p><strong>1. The computer can lie to you once. </strong>
            For one of your guesses, the clue you get will be bogus &mdash; the coloring of the letters won't match up with the secret word.
            For all your other guesses, the clue will be accurate. This rule is like in the game <a href="https://misha.fish/wordlie/" target="_blank" rel="noreferrer">Word Lie</a> (by Misha Lavrov).
            If you want to let the computer lie to you more than once, you can change that in the settings menu.</p>
            <p><strong>2. The computer chooses the secret word adversarially. </strong>
            {/* Instead of choosing a secret word ahead of time, the computer creates a pool of all 2000-ish possible secret words. */}
            {/* It essentially creates 2000 different timelines, with a different secret word in each timeline.
            Every time you make a guess, the computer makes sure you are in a timeline that will cause you the most trouble.
            Eventually, you'll be able to pin the computer down to a specific timeline */}
            {/* When you make a guess, the computer can choose which clue it wants to give you; it then discards the secret word options that don't fit that clue */}
            It doesn't pick a word ahead of time. When you make a guess, the computer can give you any clue it wants.
            Its only restriction is that at the end of the game, when you either win or give up, it needs to be able to produce a word that fits all the clues it gave you (or rather, all but one clue &mdash; remember that it's allowed to lie to you once).
            The computer is trying to make your job as hard as possible, but with each guess you make, the computer has to give you more information about the secret word &mdash; so you'll eventually pin it down to a single word.
            The adversarial word choice is like in the game <a href="https://qntm.org/files/absurdle/absurdle.html" target="_blank" rel="noreferrer">Absurdle</a> (by qntm).
            Be warned: in addition to retoactively choosing the secret word, the computer can also retoactively choose which clue is a lie.
            </p>
            <p>If you don't like a clue you get, you are welcome to tap "Undo Guess" (or hit backspace). But if you change your guess, the computer might change its secret word, too.</p>
            <p><strong>If you get stuck: </strong>
            No worries! You can click "Give Up" in the settings menu to see the answer (or at least, one possible answer).</p>
            <p><strong>It's time to play. </strong>
            Come back and read the rest of this page when you want to learn what the colors on the keyboard mean.</p>
            <div className="settings-row">
              <button className="big-button" onClick={this.setShowRulesFalse}>Play Now!</button>
            </div>
            <p><strong>Keyboard</strong></p>
            <p>The on-screen keyboard changes color to help you keep track of information you know.
            Because the computer can lie to you, there is some information you know for sure, and some information you only know provisionally. 
            The keyboard can help you keep track of all this information. (On the other hand, if you want a simpler keyboard, you can go to settings and turn off "Fancy keyboard coloring".)
            Let's say you've made two guesses:</p>
            {/* A fully-colored keyboard key shows information you know for sure.
            A key with a dot shows information that might be a lie. For example,  */}
            <div className="gameboard-explainer">
              <GameboardRow wordInfo={{guessedWord: "party", pattern: [MISS, HIT, HIT, CLOSE, HIT].join('')}} lettersSatisfied={ALL_SATISFIED} trust={UNSURE} />
              <GameboardRow wordInfo={{guessedWord: "syrup", pattern: [HIT, CLOSE, HIT, MISS, MISS].join('')}} lettersSatisfied={ALL_SATISFIED} trust={UNSURE} />
            </div>
            {/* <p>Here's what some of the keyboard keys will look like:</p> */}
            A key with a dot shows information that might be a lie. For example:
            <KeyboardExplainerRow letter="a" letterAim={[HIT, BLANK]}>
              The computer told you the exact location of the letter <strong>A</strong>, but you don't know if you can trust it because it was just one clue, which might have been a lie.
              So, you just get a colored dot.
            </KeyboardExplainerRow>
            <KeyboardExplainerRow letter="t" letterAim={[CLOSE, BLANK]}>
              The computer clued that <strong>T</strong> is in the secret word, but again, it's just one clue, so it might be a lie.
            </KeyboardExplainerRow>
            <KeyboardExplainerRow letter="u" letterAim={[MISS, BLANK]}>
              The computer said that <strong>U</strong> is not in the secret word, but it could have lied; <strong>U</strong> might be in the secret word after all.
            </KeyboardExplainerRow>
            <p>A fully-colored keyboard key shows information you know for sure. For example:</p>
            <KeyboardExplainerRow letter={"r"} letterAim={[HIT, HIT]}>
              You have two clues that say <strong>R</strong> is the third letter of the secret word, and they can't both be lies. You can be sure that <strong>R</strong> is really the third letter of the word.
            </KeyboardExplainerRow>
            <KeyboardExplainerRow letter={"p"} letterAim={[MISS, MISS]}>
              Two clues tells you that <strong>P</strong> is not in the secret word. Even if one were a lie, the other must be truth; <strong>P</strong> really isn't in the secret word.
            </KeyboardExplainerRow>
            <p>Every once in a while, you might see a key with two colors:</p>
            <KeyboardExplainerRow letter={"y"} letterAim={[HIT, CLOSE]}>
              Two clues tell you that <strong>Y</strong> is in the secret word, so it must be.
              The whole key is colored to tell you that <strong>Y</strong> is in the word.
              On top of that, one of the clues tell you that <strong>Y</strong> is in a specific position (fifth letter), so, you also get a dot indicating that you have a clue about the exact position (but it might be a lie).
            </KeyboardExplainerRow>
            <p><strong>Distrusting clues</strong></p>
            <p>The on-screen keyboard lights up to keep track of the information you know. They keyboard doesn't know which clues are lies and which are legitimate, so it treats them all skeptically. This is indicated by the circled question mark next to each row:</p>
            <div className="gameboard-explainer">
              <GameboardRow wordInfo={{guessedWord: "party", pattern: [MISS, HIT, HIT, CLOSE, HIT].join('')}} lettersSatisfied={ALL_SATISFIED} trust={UNSURE} />
              <GameboardRow wordInfo={{guessedWord: "syrup", pattern: [HIT, CLOSE, HIT, MISS, MISS].join('')}} lettersSatisfied={ALL_SATISFIED} trust={UNSURE} />
            </div>
            <p>If, however, you determine which clue is a lie, you can tap that clue to indicate your distrust. It will then appear faded:</p>
            <div className="gameboard-explainer">
              <GameboardRow wordInfo={{guessedWord: "party", pattern: [MISS, HIT, HIT, CLOSE, HIT].join('')}} lettersSatisfied={ALL_SATISFIED} trust={CONFIDENT} />
              <GameboardRow wordInfo={{guessedWord: "syrup", pattern: [HIT, CLOSE, HIT, MISS, MISS].join('')}} lettersSatisfied={ALL_SATISFIED} relativeTrust={TRUST_LESS} trust={DISTRUST} />
            </div>
            <p>The clue for the word "SYRUP" will be disregarded, and won't be reflected in the keyboard coloring.
            This is indicated by the "X" next to the clue.
            On the other hand, since you've located the lie, all the other clues must be legitimate. So, all the letters in the word "PARTY" will be fully colored on the keyboard (as indicated by the symbol next to the clue, a checkmark in a fully colored rectangle).</p>
            <p>If you determine the lie has to be one of two clues, you can tap to distrust both of them; those two clues will be treated skeptically, and all the others will be treated as certain. When in doubt, you can check how a clue will be reflected in the keyboard by looking at the symbol next to the clue.</p>
            <p>You can also make use of the keyboard coloring and distrusted clues in games with more than one lie; it will work similarly.</p>
          </div>
        </div>
      </div>
    )
  }

  setShowRulesFalse() {
    this.props.setShowRules(false);
  }
}

class SettingsModal extends React.Component {
  constructor(props) {
    super(props);
    this.setShowSettingsFalse = this.setShowSettingsFalse.bind(this);
  }
  render() {
    const atLeastHardMode = (this.props.whichHardMode === HARD_MODE || this.props.whichHardMode === EVEN_HARDER_MODE);
    return (
      <div className={"modal " + (this.props.showSettings ? "show" : "hide")}>
        <div className="main-column">
          <div className="modal-header">
            <div className="modal-close-button" />
            <div className="modal-title">Settings</div>
            <button className="modal-close-button" value="close-settings" onClick={this.setShowSettingsFalse}>&times;</button>
          </div>
          <div className="modal-content">
            <div className="settings-row">
              <button className="big-button" onClick={this.props.doGiveUp}>Give Up</button>
              <button className="big-button" onClick={this.props.doNewGame}>New Game</button>
            </div>
            {this.props.maxLiesAddMessage ? <div className="message max-lies-add-message">{this.props.maxLiesAddMessage}</div> : null}
            <OneSetting name="Number of Lies" control={<NumberInput value={this.props.maxLies} add={this.props.maxLiesAdd} off={!this.props.isNewGame} />} >
              The computer is allowed to lie (give you incorrect colors) in response to this many guesses.
            </OneSetting>
            <OneSettingSwitch name="Hard Mode" onChange={this.props.toggleHardMode} checked={atLeastHardMode} extraClassNames={(atLeastHardMode ? " continues" : null)}>
              {/* If you receive multiple clues that a letter is in the word (more than the number of allowed lies), then it must be in the secret word &mdash; and must be in your future guesses. */}
              If a letter must be in the secret word (because you've received multiple clues about it, more than the number of lies), then it must be in your future guesses.
            </OneSettingSwitch>
            {(atLeastHardMode
              ?
                <div className="settings-row">
                  <OneSettingSwitch name="Even Harder Mode" onChange={this.props.toggleEvenHarderMode} checked={this.props.whichHardMode === EVEN_HARDER_MODE} extraClassNames=" continued">
                    Every guess must be a valid possibility for the secret word &mdash; your guess can't contradict multiple past clues (more than the number of lies).
                  </OneSettingSwitch>
                </div>
              : null)}
            <OneSettingSwitch name="Fancy keyboard coloring" onChange={this.props.toggleUseFancyTrust} checked={this.props.useFancyTrust}>
              <strong>Basic keyboard:</strong> Each letter on the keyboard is colored based on what you know about it. It assumes that every clue is accurate, but you can tap on a clue to mark it as a lie. <br />
              <strong>Fancy keyboard:</strong> Fully colored letters show information that is certain. Information that might be a lie is marked with a dot.
            </OneSettingSwitch>
            <OneSettingSwitch name="Dark Mode" onChange={this.props.toggleDarkMode} checked={this.props.useDarkMode}>
              
            </OneSettingSwitch>
            <OneSettingSwitch name="High-Contrast Colors" onChange={this.props.toggleHighContrast} checked={this.props.useHighContrast}>
              
            </OneSettingSwitch>
            <OneSetting name="Feedback" control={<a href={TWITTER_LINK} target="blank">Twitter</a>}>

            </OneSetting>
            <div className="settings-row">&copy;2022 Timothy Black. v1.6.</div>
          </div>
        </div>
      </div>
    )
  }

  setShowSettingsFalse() {
    this.props.setShowSettings(false);
  }
}

