  componentDidMount() {
    window.addEventListener("keydown", this.handleKeydown);
    // window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("keydown", this.handleKeydown)
    // window.removeEventListener("scroll", this.handleScroll);
  }

  handleKeydown(event) {
    const lowerKey = event.key.toLowerCase();
    if (lowerKey.length === 1 && 'a' <= lowerKey && lowerKey <= 'z') {
      this.typeLetter(lowerKey);
    } else if (lowerKey === 'backspace') {
      this.typeBackspace();
    } else if (lowerKey === 'escape') {
      this.typeEscape();
    } else if (lowerKey === 'enter' || lowerKey === 'return') {
      this.tryEnter();
    }
  }

  typeLetter(letter) {
    // console.log(letter)
    this.setState((state) => {
      const currentGuess = state.currentGuess;
      if (currentGuess.length < WORD_LENGTH && !(checkWinStandalone(state.pastGuesses)))
        return {currentGuess: currentGuess + letter};
      else
        return {};
    })
  }

  typeBackspace() {
    this.setState((state) => {
      const currentGuess = state.currentGuess;
      const pastGuesses = state.pastGuesses;
      const pastRelativeTrust = state.pastRelativeTrust;
      if (state.isGivenUp) {
        return {
          message: '',
          maxLiesAddMessage: '',
          satisfaction: null,
          shareCopyStatus: UNSHARED,
          isGivenUp: false,
        };
      }
      else if (currentGuess.length > 0) {
        return {
          currentGuess: currentGuess.slice(0, -1),
          message: '',
          maxLiesAddMessage: '',
          satisfaction: null,
          shareCopyStatus: UNSHARED,
          isGivenUp: false,
        };
      } else if (pastGuesses.length > 0) {
        const lastWord = pastGuesses[pastGuesses.length - 1].guessedWord;
        const actualToLiesLeft = pastGuesses[pastGuesses.length - 1].actualToLiesLeft;
        return {currentGuess: lastWord,
                actualToLiesLeft: actualToLiesLeft,
                pastGuesses: pastGuesses.slice(0, -1),
                pastRelativeTrust: pastRelativeTrust.slice(0, -1),
                message: '',
                maxLiesAddMessage: '',
                satisfaction: null,
                shareCopyStatus: UNSHARED,
                isGivenUp: false,
              };
      } else {
        return {
          message: '',
          maxLiesAddMessage: '',
          satisfaction: null,
          shareCopyStatus: UNSHARED,
          isGivenUp: false,
        };
      }
    })
  }

  typeEscape() {
    if (this.state.showRules) {
      this.setShowRules(false);
    }
    if (this.state.showSettings) {
      this.setShowSettings(false);
    }
  }

  tryEnter() {
    this.setState((state) => {
      const currentGuess = state.currentGuess;
      const pastGuesses = state.pastGuesses;
      const pastRelativeTrust = state.pastRelativeTrust;
      const isGivenUp = state.isGivenUp;
      if (currentGuess.length === WORD_LENGTH && !isGivenUp) {
        if (ALL_GUESSABLE_SET.has(currentGuess)) {
          let pattern;
          const [satisfiesHardMode, satisfaction, message] = checkHardHarderModeStandalone(currentGuess, pastGuesses, state.whichHardMode, state.maxLies);
          if (satisfiesHardMode) {
            let outgoingActualToLiesLeft;
            if (state.actualToLiesLeft === null) {
              outgoingActualToLiesLeft = {};
              // console.log("maxLies", state.maxLies);
              ALL_ACTUAL.forEach((actualOption) => {
                outgoingActualToLiesLeft[actualOption] = state.maxLies;
              });
              // console.log("anew", outgoingActualToLiesLeft);
            } else {
              outgoingActualToLiesLeft = state.actualToLiesLeft;
            }
            if (state.isAbsurd) {
              pattern = choosePatternMaximizeTurns(outgoingActualToLiesLeft, currentGuess);
            } else {
              pattern = computePattern(state.actualWord, currentGuess);
            }
            const newActualToLiesLeft = filterLiesLeftOnePattern(outgoingActualToLiesLeft, currentGuess, pattern);
            console.log(currentGuess, newActualToLiesLeft);
            const newPastGuesses = [...pastGuesses, {guessedWord: currentGuess, pattern: pattern, actualToLiesLeft: state.actualToLiesLeft}];
            const newIsWin = checkWinStandalone(newPastGuesses);
            const [/*numUnsatisfied*/, newSatisfaction] = (newIsWin ? computeSatisfactionStandalone(currentGuess, newPastGuesses) : [null, null]);
            // console.log(newSatisfaction);
            return {pastGuesses: newPastGuesses,
                    pastRelativeTrust: [...pastRelativeTrust, TRUST_MORE],
                    currentGuess: '',
                    actualToLiesLeft: newActualToLiesLeft,
                    message: '',
                    maxLiesAddMessage: '',
                    satisfaction: newSatisfaction,
                    shareCopyStatus: UNSHARED,
                    isGivenUp: false,};
          } else { // not satisfiesHardMode
            return {
              message: message,
              maxLiesAddMessage: '',
              satisfaction: satisfaction,
              shareCopyStatus: UNSHARED,
              isGivenUp: false,
            };
          }
        } else { // not in word list
          return {
            message: 'Not in word list',
            maxLiesAddMessage: '',
            satisfaction: null,
            shareCopyStatus: UNSHARED,
            isGivenUp: false,
          };
        }
      } else {
        return {};
      }
    },
    this.scrollToBottom);
  }

