  maxLiesAdd(summand) {
    this.setState((state) => {
      const isNewGame = checkNewGameStandalone(state.pastGuesses);
      if (isNewGame) {
        const newMaxLies = state.maxLies + summand;
        if (newMaxLies >= 0) {
          return {maxLies: newMaxLies}
        } else {
          return {};
        }
      } else {
        return {maxLiesAddMessage: "Number of lies can only be changed at the start of a round"}
      }
    },
      () => {localStorageSet('maxLies', this.state.maxLies);}
    )
  }
