function getGameSpecficStateStandalone(isAbsurd) {
  const actualToLiesLeft = null;
  let actualWord = null;
  if (!isAbsurd) {
    actualWord = ALL_ACTUAL[Math.floor(Math.random() * ALL_ACTUAL.length)];
  }
  const gameSpecificState = {
    pastGuesses: [], 
    pastRelativeTrust: [], 
    // relativeToAbsoluteTrust: this.computeRelativeToAbsoluteTrust(useFancyTrust, [], maxLies),
    currentGuess: '', 
    actualToLiesLeft: actualToLiesLeft,
    actualWord: actualWord, 
    isGivenUp: false,
    message: '',
    maxLiesAddMessage: '',
    satisfaction: null,
    shareCopyStatus: UNSHARED,
  }
  return gameSpecificState;
}

