function computeKeyboardAimStandalone(pastGuesses, pastRelativeTrust, relativeToAbsoluteTrust, numTrustLess, maxLies) { // does not use "this"
  // console.log('computeKeyboardAim');
  // const pastGuesses = this.state.pastGuesses;
  // const pastRelativeTrust = this.state.pastRelativeTrust;
  // const relativeToAbsoluteTrust = this.state.relativeToAbsoluteTrust;
  // const maxLies = this.state.maxLies;
  // console.log('maxLies', maxLies);
  const confidentThreshold = (numTrustLess !== null && numTrustLess < maxLies ? maxLies - numTrustLess + 1 : maxLies + 1);
  const keyboardAimDetailed = computeKeyboardAimDetailedStandalone(pastGuesses, pastRelativeTrust, relativeToAbsoluteTrust, confidentThreshold);
  // console.log('keyboardAimDetailed', keyboardAimDetailed)

  const keyboardAimSummary = {}
  for (const letter in keyboardAimDetailed) {
    const letterAim = keyboardAimDetailed[letter];
    const numHitByIndex = letterAim.numHitByIndex;
    const numHitClose = letterAim.numHitClose;
    const numMiss = letterAim.numMiss;
    let maxHit = 0;
    // let totalClose = numClose;
    numHitByIndex.forEach((count, index) => {
      if (count > maxHit) {maxHit = count;}
      // totalClose += count;
    })
    let highMarks = {1: BLANK, [confidentThreshold]: BLANK};
    for (const threshold in highMarks) {
      if (maxHit >= threshold) {highMarks[threshold] = HIT;}
      else if (numHitClose >= threshold) {highMarks[threshold] = CLOSE;}
      else if (numMiss >= threshold) {highMarks[threshold] = MISS;}
    }
    keyboardAimSummary[letter] = [highMarks[1], highMarks[confidentThreshold]];
  }
  return keyboardAimSummary;
}

function computeKeyboardAimDetailedStandalone(pastGuesses, pastRelativeTrust, relativeToAbsoluteTrust, confidentThreshold) { // doesn't use "this" // I think this doesn't need to be separated from computeKeyboardAimStandalone after all
  const keyboardAimDetailed = {};
  ALL_LETTERS.forEach((letter) => {keyboardAimDetailed[letter] = {numHitByIndex: Array(WORD_LENGTH).fill(0), numHitClose: 0, numMiss: 0}})
  pastGuesses.forEach((wordInfo, rowNum) => {
    // console.log('relativeToAbsoluteTrust', relativeToAbsoluteTrust)
    const trust = ((pastRelativeTrust === null) ? UNSURE : relativeToAbsoluteTrust[pastRelativeTrust[rowNum]]);
    // const trust = relativeToAbsoluteTrust[pastRelativeTrust[rowNum]];
    // const weightConversion = {[DISTRUST]: 0, [UNSURE]: 1, [CONFIDENT]: 2};
    const weight = {[DISTRUST]: 0, [UNSURE]: 1, [CONFIDENT]: confidentThreshold}[trust];
    if ((trust === UNSURE) || (trust === CONFIDENT)) {
      const uniqueLetterAim = {};
      for (const i in wordInfo.guessedWord) {
        const letter = wordInfo.guessedWord[i];
        const aim = wordInfo.pattern[i];
        // console.log('aim', i, wordInfo.guessedWord[i], wordInfo.pattern)
        if (aim === HIT) {keyboardAimDetailed[letter].numHitByIndex[i] += weight;}
        if (letter in uniqueLetterAim) {
          if (higherAim(aim, uniqueLetterAim[letter])) {uniqueLetterAim[letter] = aim;}
        } else {
          uniqueLetterAim[letter] = aim;
        }
      }
      for (const letter in uniqueLetterAim)
      {
        const aim = uniqueLetterAim[letter];
        // console.log('trust', trust, 'weight', weight)
        if (aim === HIT || aim === CLOSE) {keyboardAimDetailed[letter].numHitClose += weight;}
        else if (aim === MISS) {keyboardAimDetailed[letter].numMiss += weight;}
        // else if (aim === HIT) {}
        else {console.log("should not reach this line of code");}
      }
    }
  })
  return keyboardAimDetailed;
}

function checkHardHarderModeStandalone(currentGuess, pastGuesses, whichHardMode, maxLies) { // doesn't use this
  // console.log("checkHardHarderModeStandalone(currentGuess, pastGuesses, whichHardMode, maxLies)", currentGuess, pastGuesses, whichHardMode, maxLies)
  if (whichHardMode === NOT_HARD_MODE) {
    return [true, null, null];
  } else if (whichHardMode === HARD_MODE) {
    const hitUnsatisfied = Array(WORD_LENGTH); // position in word to letter to number of past guesses in which that letter appears as HIT (but does not appear in this position in currentGuess)
    for (let i = 0; i < WORD_LENGTH; i++) {
      hitUnsatisfied[i] = {};
    }
    const closeUnsatisfied = {}; // letter to number of past guesses where the number of HIT or CLOSE appearances of that letter exceeds the number in the currentGuess
    const currentGuessLetterCount = {};
    [...currentGuess].forEach((letter) => {
      setDefaultThenAdd(currentGuessLetterCount, letter, 0, 1);
    })
    pastGuesses.forEach((pastGuess) => {
      const numAppearancesInPastWord = {};
      [...pastGuess.pattern].forEach((aim, index) => {
        const currentLetter = currentGuess[index];
        const pastLetter = pastGuess.guessedWord[index];
        if (aim === HIT || aim === CLOSE) {
          setDefaultThenAdd(numAppearancesInPastWord, pastLetter, 0, 1);
        }
        if (aim === HIT) {
          if (currentLetter !== pastLetter) {
            setDefaultThenAdd(hitUnsatisfied[index], pastLetter, 0, 1);
          }
        }
      });
      for (const letter in numAppearancesInPastWord) {
        const pastCount = numAppearancesInPastWord[letter];
        const currentCount = getDefault(currentGuessLetterCount, letter, 0);
        if (pastCount > currentCount) {
          setDefaultThenAdd(closeUnsatisfied, letter, 0, 1);
        }
      }
    });
    let satisfiesHardMode = true;
    let message = null;
    hitUnsatisfied.forEach((letterToNumUnsatisfied, index) =>{
      for (const letter in letterToNumUnsatisfied) {
        const numUnsatisfied = letterToNumUnsatisfied[letter];
        // console.log('numUnsatisfied', letter, numUnsatisfied);
        if (satisfiesHardMode && numUnsatisfied > maxLies) {
          satisfiesHardMode = false;
          message = getOrdinal(index + 1) + " letter must be " + letter.toUpperCase();
        }
      }
    });
    for (const letter in closeUnsatisfied) {
      const numUnsatisfied = closeUnsatisfied[letter];
      // console.log('numUnsatisfied', letter, numUnsatisfied);
      if (satisfiesHardMode && numUnsatisfied > maxLies) {
        const currentCount = getDefault(currentGuessLetterCount, letter, 0);
        satisfiesHardMode = false;
        message = "Must contain " + (currentCount === 0 ? "" : "another ") + letter.toUpperCase();
      }
    }
    // console.log("hard mode", hitUnsatisfied, closeUnsatisfied)
    return [satisfiesHardMode, null, message];
    // return [false, null, "hard mode not properly implemented yet"];
    // // first version (for the harder version of hard mode)
    // console.log("not implemented");
    // // const keyboardAimDetailed = computeKeyboardAimDetailedStandalone(pastGuesses, null, null, maxLies);
    // // console.log('keyboardAimDetailed', keyboardAimDetailed);
    // const hitUnsatisfied = Array(WORD_LENGTH).fill({}); // position in word to letter to number of past guesses in which that letter appears as HIT (but does not appear in this position in currentGuess)
    // const closeUnsatisfied = {}; // letter to number of past guesses where the number of HIT or CLOSE appearances of that letter exceeds the number in the currentGuess
    // const currentGuessLetterCount = {};
    // [...currentGuess].forEach((letter) => {
    //   setDefaultThenAdd(currentGuessLetterCount, letter, 0, 1);
    // })
    // pastGuesses.forEach((pastGuess) => {
    //   const numAppearancesInPastWord = {};
    //   [...pastGuess.pattern].forEach((aim, index) => {
    //     const currentLetter = currentGuess[index];
    //     const pastLetter = pastGuess.guessedWord[index];
    //     if (aim === HIT || aim === CLOSE) {
    //       setDefaultThenAdd(numAppearancesInPastWord, pastLetter, 0, 1);
    //     }
    //     if (aim === HIT) {
    //       if (currentLetter !== pastLetter) {
    //         setDefaultThenAdd(hitUnsatisfied[index], pastLetter, 0, 1);
    //       }
    //     }
    //   });
    //   for (const letter in numAppearancesInPastWord) {
    //     const pastCount = numAppearancesInPastWord[letter];
    //     const currentCount = getDefault(currentGuessLetterCount, letter, 0);
    //     if (pastCount > currentCount) {
    //       setDefaultThenAdd(closeUnsatisfied, letter, 0, 1);
    //     }
    //   }
    // });
    // return [false, null, "hard mode not properly implemented yet"];
  // } else if (whichHardMode === HARD_MODE || whichHardMode === EVEN_HARDER_MODE) {
  } else if (whichHardMode === EVEN_HARDER_MODE) {
    const [numUnsatisfied, satisfaction] = computeSatisfactionStandalone(currentGuess, pastGuesses);
    if (numUnsatisfied <= maxLies) {
      return [true, null, null];
    } else {
      // const message = numUnsatisfied + " clue(s) are unsatisfied";
      // const message = "Guess contradicts " + numUnsatisfied + " clue" + (numUnsatisfied == 1 ? "" : "s");
      const message = numUnsatisfied + " " + (numUnsatisfied === 1 ? "clue is" : "clues are") + " unsatisfied";
      // const message = "More than " + maxLies + " " + (maxLies === 1 ? "clue is" : "clues are") + " unsatisfied";
      return [false, satisfaction, message];
    }
  } else {
    console.log("should not reach this line");
  }
}

function checkAllSatisfiesHardHarderModeStandalone(pastGuesses, whichHardMode, maxLies) {
  // console.log("checkAllSatisfiesHardHarderModeStandalone(pastGuesses, whichHardMode, maxLies)", pastGuesses, whichHardMode, maxLies);
  // console.log("checking whichHardMode", whichHardMode);
  // pastGuesses.forEach((pastGuess, index) => {
  for (let index in pastGuesses) {
    const pastGuess = pastGuesses[index];
    // console.log("checking", pastGuess.guessedWord);
    const [satisfiesHardMode, /*satisfaction*/, /*message*/] = checkHardHarderModeStandalone(pastGuess.guessedWord, pastGuesses.slice(0, index), whichHardMode, maxLies);
    // console.log("satisfiesHardMode", satisfiesHardMode);
    if (!satisfiesHardMode) {
      // console.log("pastGuess", pastGuess.guessedWord, index);
      return false;
    }
  }
  // console.log("passed checks");
  return true;
}

function computeSatisfactionStandalone(currentGuess, pastGuesses) {
  let numUnsatisfied = 0;
  let satisfaction = [];
  // let unsatsifedSet = new Set();
  pastGuesses.forEach((pastGuess, index) => {
    let lettersSatisfied = "";
    const hypotheticalPattern = computePattern(currentGuess, pastGuess.guessedWord);
    if (pastGuess.pattern === hypotheticalPattern) {
      lettersSatisfied = ALL_SATISFIED;
    } else {
      // console.log("pattern", hypotheticalPattern, pastGuess.pattern);
      let wordIsUnsatisfied = false;
      // unsatsifedSet.add(index);
      [...pastGuess.pattern].forEach((letterAim, i) => {
        const hypotheticalAim = hypotheticalPattern[i];
        // if (letterAim === hypotheticalAim || (whichHardMode === HARD_MODE && higherAim(hypotheticalAim, letterAim))) { // this doesn't do the right thing for hard mode (it's okay for even harder mode) if the currentGuess has the same letter twice, and the later instance is in the correct place
        if (letterAim === hypotheticalAim) { // this doesn't do the right thing for hard mode (it's okay for even harder mode) if the currentGuess has the same letter twice, and the later instance is in the correct place
            lettersSatisfied += LETTER_SATISFIED;
        } else {
          lettersSatisfied += LETTER_UNSATIFIED;
          wordIsUnsatisfied = true;
        }
      })
      if (wordIsUnsatisfied) {
        numUnsatisfied += 1;
      }
    }
    satisfaction.push(lettersSatisfied);
  })
  return [numUnsatisfied, satisfaction];
}

function getGameboardEmoji(pastGuesses, useDarkMode, useHighContrast) {
  const isWin = checkWinStandalone(pastGuesses);
  const actualWord = (isWin ? pastGuesses[pastGuesses.length - 1].guessedWord : null);
  const [/*numUnsatisfied*/, satisfaction] = (isWin ? computeSatisfactionStandalone(actualWord, pastGuesses) : [null, null]);
  const aimToEmoji = DARK_CONTRAST_AIM_TO_EMOJI[useDarkMode][useHighContrast];
  // console.log("aimToEmoji", aimToEmoji);
  let s = ''
  pastGuesses.forEach((pastGuess, index) => {
    const isLie = (satisfaction !== null && satisfaction[index] !== ALL_SATISFIED);
    [...pastGuess.pattern].forEach((aim) => {
      s += aimToEmoji[aim];
    })
    if (isLie) {
      s += " 🤥 LIE"
    }
    if (pastGuess.pattern === ALL_HIT) {
      s += "\n🏆 " + pastGuess.guessedWord.toUpperCase() + " 🏆";
    }
    s += '\n';
  })
  if (!isWin) {
    s += Array(WORD_LENGTH).fill(aimToEmoji[BLANK]).join('') + " Gave Up\n";
  }
  return s;
}

function checkWinStandalone(pastGuesses) {
  // console.log("pastGuesses", pastGuesses);
  return (pastGuesses.length > 0) && (pastGuesses[pastGuesses.length - 1].pattern === ALL_HIT);
}

function checkNewGameStandalone(pastGuesses) {
  // console.log("pastGuesses", pastGuesses);
  return (pastGuesses.length === 0);
}

function miniMemo(fn) {
  // console.log("miniMemo setup");
  let savedArgs = null;
  let savedValue = null;
  function g(...args) {
    // console.log("miniMemo outer", savedArgs, args);
    if (savedArgs === null) {
      // console.log("miniMemo inner (null)");
      savedArgs = args;
      savedValue = fn(...args);
      return savedValue;
    }
    for (let i = 0; i < savedArgs.length; i++) {
      if (savedArgs[i] !== args[i]) {
        // console.log("miniMemo inner", i);
        savedArgs = args;
        savedValue = fn(...args);
        return savedValue;
      }
    }
    // if (!(savedArgs == args)) {
    //   console.log("miniMemo inner");
    //   savedArgs = args;
    //   savedValue = fn(...args);
    // }
    return savedValue;
  }
  return g;
}

